import React from "react";
import Navbar from "./Components/Navbar";
import './App.css';
import Home from "./Components/Home"

function App() {
  const [loading,setLoading]=React.useState(3000);
  React.useEffect(() => {
    setTimeout(() => setLoading(0), loading);
  },[]);
  return(
    <div className="App">
      {loading !==0?(<div className="loadingMain">Loading...</div>):
      ("")}
      <Navbar/>
      <Home/>
    </div>
  )
}

export default App;
