import React from 'react'
import style from "../Styles/Home.module.css";
import Honey from "../Components/Home/honey"

const Home = () => {

  return (
    <div className={style.main}>
      <div className={style.mainSec}>
        <div className={style.details}>
          <h1>Hello 👋.</h1>
          <h3>I'm Rajkumar Pradhan.</h3>
          <h3>Aspiring Full Stack Web Developer at Masai School.</h3>
        </div>
        <div className={style.honey}>
          <Honey />
        </div>
        
      </div>
    </div>
  )
}

export default Home